@import '../core/helpers.scss';

.growing {
	.growing__wrapper {
		max-width: 1165px;
	}

	h2 {
		font-size: 32px;
	}

	.growing__copy {
		font-family: 'Open Sans', sans-serif;
		font-size: 20px;
		line-height: 1.6;
		min-height: 160px;
		color: #8F93A8;
	}

	.slick-list {
		margin-right: -80px;
	}

	.growing__item {
		margin-left: 0;
		margin-right: 80px;
	}

	.growing__image {
		img {
			width: 100%;
		}
	}

	.growing__arrows {
		display: flex;
	}

	.growing__arrow {
		margin-right: 30px;
		cursor: pointer;
		@extend .trans;
	}

	.growing__arrow--left {
		&:hover {
			margin-left: -5px;
		}
	}

	.growing__arrow--right {
		&:hover {
			margin-right: -5px;
		}
	}
}